function Hubcounter() {
    const [data, setData] = useState([])
    const [count, setCount] = useState(0)

    useEffect(() => {
        if (count < 1) {
            const link = 'https://api.github.com/users/HackerTon/repos'

            fetch(link)
                .then(res => {
                    return res.json()
                })
                .then(json => {
                    json.sort((a, b) => {
                        if (a.updated_at < b.updated_at) {
                            return 1;
                        } else {
                            return -1;
                        }
                    })


                    setData(json)
                })
                .catch(e => {
                    console.error(e)

                })

            setCount(count + 1)

        }


    })

    return (
        <div className='table' >
            <table className='table table-hover'>
                <thead className='thead-dark'>
                    <tr>
                        <th scope='col'>Name</th>
                        <th scope='col'>Username</th>
                        <th scope='col'>Description</th>
                        <th scope='col'>Last update</th>

                    </tr>
                </thead>
                <tbody>
                    {
                        data.map(slot => {
                            const date = new Date(slot.updated_at)
                            const year = date.getFullYear()

                            return (
                                <tr>
                                    <th scope='row'><a href={slot.html_url}>{slot.name}</a></th>
                                    <td>{slot.owner.login}</td>
                                    <td>{slot.description}</td>
                                    <td>{year}</td>
                                </tr>

                            )
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}

class Button extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            passTrue: true,
        };

        this.onclick = this.onclick.bind(this)

    }

    onclick() {
        this.setState(state => ({
            passTrue: !state.passTrue
        }));
    }

    render() {
        return (
            <div>
                <button onClick={this.onclick}>{this.state.passTrue ? 'You Found mE' : 'Oi!'}</button>

                {this.state.passTrue ? '' : 'Press! Press! Press! You kid ahhh?!'}
            </div>
        )
    }
}


